.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.col {
  color: #61dafb;
}
.flex-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 50%;
  border: 1% black solid;
  margin: 0 auto;
}

.flex-item {
  width: 25%;
  height: 30%;
  border: 1% black solid;
  background: #369;
}

/* Scrollbar styles */
::-webkit-scrollbar {
  width: 1.5%;
  height: 1%;
}

::-webkit-scrollbar-track {
  border: 1px solid black;
  border-radius: 1%;
}

::-webkit-scrollbar-thumb {
  background: black;
  border-radius: 10px;
}

/* Add some padding inside the card container */
.container {
  padding: 2% 3%;
}
.invisible{
  visibility: hidden;
}

table.dataSheet {
  table-layout: fixed;
  border: 1px solid #000000;
  background-color: grey;
  width: 100%;
  text-align: left;
  border-collapse: collapse;
}
table.dataSheet td, table.dataSheet th {
  border: 1px solid #AAAAAA;
  padding: 3px 2px;
}

table.dataSheet tbody td {
  font-size: 13px;
}
table.dataTable tbody td input {
  margin-left:30%;
}
table.dataSheet tr:nth-child(even) {
  background: #FFFFFF;
}
table.dataSheet thead {
  background: #FFFFFF;
  background: -moz-linear-gradient(top, #ffffff 0%, #ffffff 66%, #FFFFFF 100%);
  background: -webkit-linear-gradient(top, #ffffff 0%, #ffffff 66%, #FFFFFF 100%);
  background: linear-gradient(to bottom, #ffffff 0%, #ffffff 66%, #FFFFFF 100%);
  border-bottom: 3px solid #444444;
}
table.dataSheet thead th {
  font-size: 15px;
  font-weight: bold;
  color: #000000;
  text-align: center;
  border-left: 1px solid #000000;
}
table.dataSheet thead th:first-child {
  border-left: none;
}
table.dataSheet tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #FFFFFF;
  background: #F5F5F5;
  background: -moz-linear-gradient(top, #f7f7f7 0%, #f6f6f6 66%, #F5F5F5 100%);
  background: -webkit-linear-gradient(top, #f7f7f7 0%, #f6f6f6 66%, #F5F5F5 100%);
  background: linear-gradient(to bottom, #f7f7f7 0%, #f6f6f6 66%, #F5F5F5 100%);
  border-top: 2px solid #444444;
}
table.dataSheet tfoot td {
  font-size: 14px;
}
table.dataSheet tfoot .links {
  text-align: right;
}
table.dataSheet tfoot .links a{
  display: inline-block;
  background: grey;
  color: #FFFFFF;
  padding: 2% 3%;
  border-radius: 5px;
}
.card .close{
  position: absolute;
  cursor: pointer;
  top: 10px;
  right: 10px;
}
.btngroup {
  display: flex;
}
.btngroup .button {
  background-color: rgb(192, 192, 192);
  border: 1px solid black;
  color: black;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 90%;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
}
.addPortfolio button{
  background-color: grey;
  border: 1px solid black;
  color: black;
  padding: 10px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 90%;
  -webkit-transition-duration: 0.4s;
  transition-duration: 0.4s;
  cursor: pointer;
}
.btn-group {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.btn-group button {
  background-color: rgb(192, 192, 192); /* Grey background
  border: 1px solid black;
  color: black; /* White text */
  padding: 1% 2%; /* Some padding */
  margin: 5%;
  cursor: pointer; /* Pointer/hand icon */
  float: left; /* Float the buttons side by side */
}

.card {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: auto;
  height: 500px;
  padding:2%;
  max-width: 500px;
  min-height: 600px;
  float:left;
  position: relative;
  border: 5px solid black;
}

/* Clear floats (clearfix hack) */
.btn-group:after {
  content: "";
  clear: both;
  display: table;
}


table {
  margin: 3%;
}
.table-wrapper {
  width: 100%;
  height:200px;
  overflow:scroll;
}
.card-list-table {
  background: white;
}
.addPortfolio{
  padding: 3%;
  display: flex;
}
a.close{
  position: relative;
  margin-top:-3%;
  margin-right:-3%;
  cursor:pointer;
  color: #fff;
  border: 1px solid #AEAEAE;
  border-radius: 30px;
  background: #605F61;
  font-size: 31px;
  font-weight: bold;
  display: inline-block;
  line-height: 0px;
  padding: 11px 3px;
}

